import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

export default function Login() {
	const [User, setUser] = useState({
		username: '',
		password: '',
		promotionalcode: '',
	});

	const history = useHistory();

	function handleSubmit(e) {
		e.preventDefault();
		login(e);
	}

	const login = async (event) => {
		let formData = new FormData();

		Object.entries(User).map(([key, value]) => {
			formData.append(key, value);
			return null;
		});

		const request = {
			method: 'POST',
			body: formData,
		};

		fetch(
			'https://pruebas.ferreteriaonlinehoyostools.com/admin/frontend/web/index.php/login/user-login-vr',
			request
		)
			.then((response) => response.json())
			.then((data) => {
				console.log(data);
				if (!data.error) {
					history.push('/home');
					localStorage.setItem('htok', data.data.token);
				} else {
					alert(`${data.message}`);
					event.stopPropagation();
				}
			})
			.catch((error) => {
				alert('Error al realizar la petición');
				event.stopPropagation();
			});

		setUser({
			username: '',
			password: '',
			promotionalcode: '',
		});

		/* const response = await fetch(
			'https://pruebas.ferreteriaonlinehoyostools.com/admin/frontend/web/index.php/login/user-login-vr',
			request
		);

		if (response.ok) {
			console.log(response);
		} else {
			event.stopPropagation();
			alert('Error al realizar la petición');
		} */
		/* firebase
			.auth()
			.signInWithEmailAndPassword(User.username, User.password)
			.then((resolve) => {
				SetStateRoute(true);
				history.push('/test');
			})
			.catch(function (error) {
				// Handle Errors here.
				alert('No se pudo iniciar sesion');
				// ...
			}); */
	};

	function handleChangeInput(e) {
		const inputName = e.target.name;
		const inputValue = e.target.value;
		setUser({ ...User, [inputName]: inputValue });
	}

	return (
		<div className="container-fluid vh-100">
			<div className="row h-100">
				<div
					className="col-md-7 d-sm-none d-none d-md-block p-0 h-100"
					style={{
						backgroundImage: `url(
							'${process.env.PUBLIC_URL}/Assets/images/hoyostools.jpeg'
						)`,
						backgroundColor: '#fff',
						backgroundPosition: 'center',
						backgroundRepeat: 'no-repeat',
						backgroundSize: 'cover',
					}}
				></div>
				<div className="col-md-5 col-xs-12 d-flex align-items-center border shadow pr-0 bg-white">
					<div className="card bg-white border-0 w-100">
						<div className="card-header bg-white d-flex flex-column align-items-center justify-content-center bg-white border-bottom-0">
							<img
								src={`${process.env.PUBLIC_URL}/Assets/images/new-logo-header.png`}
								alt=""
								width="300"
							/>
							<h5 className="mt-4">
								Acceso Usuarios Registrados
							</h5>
							<p className="text-center mb-0">
								Por favor ingrese sus datos de acceso
							</p>
						</div>
						<div className="card-body bg-white">
							<p className="text-danger">*Obligatorio</p>
							<form onSubmit={handleSubmit}>
								<div className="form-group">
									<label htmlFor="usuario">
										Usuario{' '}
										<span className="text-danger">*</span>
									</label>
									<input
										type="text"
										className="form-control"
										id="usuario"
										aria-describedby="emailHelp"
										name="username"
										placeholder="correo@correo.com"
										onChange={handleChangeInput}
										required
									/>
									<div className="valid-feedback">
										Tiene buena pinta!
									</div>
									<div className="invalid-feedback">
										Por favor, elija un nombre de usuario valido.
									</div>
								</div>
								<div className="form-group">
									<label htmlFor="Password">
										Contraseña{' '}
										<span className="text-danger">*</span>
									</label>
									<input
										type="password"
										className="form-control"
										name="password"
										id="Password"
										placeholder="⁕⁕⁕"
										onChange={handleChangeInput}
										required
									/>
									<div className="valid-feedback">
										Tiene buena pinta!
									</div>
									<div className="invalid-feedback">
										Error en la contraseña.
									</div>
								</div>
								<div className="form-group">
									<label htmlFor="Password">
										Código Promocional
									</label>
									<input
										type="password"
										className="form-control"
										name="promotionalcode"
										id="promotionalcode"
										placeholder="1A578"
										onChange={handleChangeInput}
									/>
									<small
										id="codeHelp"
										className="form-text text-muted"
									>
										Si tienes un código promocional, ingresalo en
										esta casilla.
									</small>
								</div>
								<button
									type="submit"
									className="btn btn-primary btn-block my-4"
								>
									Ingresar
								</button>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
